import { Component, OnInit } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { AlertService } from 'src/app/services/alert.service';
import {InvoiceService} from 'src/app/services/Invoice/invoice.service';
import { Invoice } from 'src/app/Model/Invoice';

@Component({
  selector: 'app-view-invoice',
  templateUrl: './view-invoice.component.html',
  styleUrls: ['./view-invoice.component.scss']
})
export class ViewInvoiceComponent implements OnInit {

  search:string;
  isVisible=false;
  listData:Invoice[]=[];
  modalTitle="Create new Invoice";
  invoiceObserverSubject:Subject<Invoice> = new Subject();
  contactSubject:Subscription;
  invoice= new Invoice();
  showInvoice=false;

  constructor( private _invoiceService:InvoiceService,
    private _notification:AlertService) { }

  ngOnInit(): void {
    this._invoiceService.invoiceObserver$.subscribe(res=>{
      if(res)
        this.listData=res;
      this.isVisible=false;
    });
    this._invoiceService.GetInvoice();
  }
  handleModalOpen()
  {
    this.isVisible=true;
    this.modalTitle="New Company";
    this.invoiceObserverSubject.next(null);
    this.showInvoice=false;
  }
  handleCancel()
  {
    this.isVisible=false;
    this.showInvoice=false;
  }
  handleEdit(data:Invoice)
  {
    this.modalTitle="Edit Company";
    this.isVisible=true;
    this.showInvoice=false;
    this.invoiceObserverSubject.next(data);
  }
  showSingleInvoice(data:Invoice)
  {
    this.invoice=data;
    this.showInvoice=true;
  }DeleteInvoice(data:Invoice){ 
    this._notification.delete().then(result=>{
      if(result.isConfirmed){
        this._invoiceService.DeleteInvoice(data.id).subscribe(res=>{
          if(res.isSuccessfull){
            this._invoiceService.GetInvoice();
            this._notification.success('Invoice Deleted Successfully!')
          }
          else{
            this._notification.error('Error while deleting Invoice!');
          }
        });

      }
      else{
        return;
      }
    });
    this.showInvoice=false;
  }
  
}
