import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { Invoice } from 'src/app/Model/Invoice';

@Injectable({
  providedIn: 'root'
})
export class InvoiceService  extends BaseApiHandlerService {
  invoiceObserver$:Observable<Invoice[]>;

  private invoiceSubject$: BehaviorSubject<Invoice[]> = new BehaviorSubject<Invoice[]>(undefined);

  public get invoices() {return this.invoiceSubject$.value;}
  constructor(private http:HttpClient) 
  {
    super(http)
    this.invoiceObserver$=this.invoiceSubject$.asObservable();
  }
  SaveInvoice(data)
  {
    return this.Post(data,API_URL+API_ENDPOINTS.Invoice);
  }
  UpdateInvoioce(data:Invoice)
  {
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Invoice)
  }
  GetInvoice()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Invoice).subscribe(res=>{
      this.invoiceSubject$.next(res.dynamicResult);
    });
  }
  DeleteInvoice(id)
  {
    return this.Delete(id,API_URL+API_ENDPOINTS.Invoice);
  }
}
   